import Vue from 'vue'
import Router from 'vue-router'
const bind = () => import('@/bind/index')
const userManage = () => import('@/bind/userManage')
const addUser = () => import('@/bind/addUser')
const frame = () => import('@/frame/index')
const projectFrame = () => import('@/frame/projectFrame')
const newProject = () => import('@/newProject/index')
const project = () => import('@/project/index')
const mission = () => import('@/mission/index')
const pace = () => import('@/pace/index')
const projectDetail = () => import('@/project/project')
const module = () => import('@/project/module')
const moduleMission = () => import('@/project/mission')
const missionDetail = () => import('@/mission/detail')
const noProject = () => import('@/project/components/noProject')
const projectPace = () => import('@/pace/projectPace')
const missionPace = () => import('@/pace/project/mission')
const examine = () => import('@/newProject/teacherComponents/examine')
const Gain = () => import('@/mission/gain')
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'bind',
      component: bind
    },
    {
      path: '/userManage',
      name: 'userManage',
      component: userManage
    },
    {
      path: '/addUser',
      name: 'addUser',
      component: addUser
    },
    {
      path: '/frame',
      name: 'frame',
      component: frame
    },
    {
      path: '/frame/:pName/:pId',
      name: 'projectFrame',
      component: projectFrame
    },
    {
      path: '/newProject',
      name: 'newProject',
      component: newProject
    },
    {
      path: '/newProject/:pId',
      name: 'examine',
      component: examine
    },
    {
      path: '/noProject',
      name: 'noProject',
      component: noProject
    },
    {
      path: '/project',
      name: 'project',
      component: project
    },
    {
      path: '/project/:pId',
      name: 'projectDetail',
      component: projectDetail
    },
    {
      path: '/project/module/:moId',
      name: 'module',
      component: module
    },
    {
      path: '/project/module/mission/:miId',
      name: 'moduleMission',
      component: moduleMission
    },
    {
      path: '/mission',
      name: 'mission',
      component: mission
    },
    {
      path: '/mission/:miId',
      name: 'missionDetail',
      component: missionDetail
    },
    {
      path: '/gain',
      name: 'Gain',
      component: Gain
    },
    {
      path: '/pace',
      name: 'pace',
      component: pace
    },
    {
      path: '/pace/:pId',
      name: 'projectPace',
      component: projectPace
    },
    {
      path: '/pace/mission/:miId',
      name: 'missionPace',
      component: missionPace
    }
  ]
})



