const api = 'http://bci.renjiwulian.com'
const PI = 6
const PR = 4
const assistant = 5
const student = 3
const stranger = 2
export default {
  url: api,
  getUrlKey: function (name) {
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [,""])[1].replace(/\+/g, '%20')) || null
  },
  PI: PI,
  PR: PR,
  assistant: assistant,
  student: student,
  stranger: stranger
}



